import React from 'react'
import headerarrow from "../../images/SEO/headerarrow.webp"
import dataanalytics from "../../images/SEO/dataanalytics.webp"
import Feedback from "../../images/SEO/feedback.webp"
import valuations from "../../images/SEO/valuations.webp"
import MRR from "../../images/SEO/MRR.webp"
import seoheader from "../../images/SEO/seoheader.webp"
import om from "../../images/SEO/orangemoon.webp"
import bm from "../../images/SEO/bluemoon.webp"


const Header = () => {
    return (
       <section className="seoHeader">
<div className="om"  style={{width:"20px"}}>
  <img src={om}  alt=""   />
</div>

<div className="bm" style={{width:"20px"}}>
<img src={bm}  alt=""  />
</div>

<div className="om1" alt="" style={{width:"20px"}}>
  <img src={om}  alt="" alt=""   />
</div>

<div className="bm1" style={{width:"20px"}}>
<img src={bm}  alt=""  />
</div>
           <div className="container">


               <div className="row">

                   <div className="col-md-6  sh-L">
                      
                    <h5> <span><img src={headerarrow}/></span>Get a FREE Quote today!</h5>
                         <h1>Professional SEO Services That Make The Difference</h1>
                         <p>Understand and Reach Customers in Key Moments That Matter</p>

                         <div className="seocontent   d-flex ">
                             

                        
                         <div  >
                               <div className=" d-flex mb-4 " >
                               <img src={dataanalytics} />
                                 <p  className=" ms-3 " >Increase Traffic</p>
                               </div>
                               <div className="d-flex" >
                                <img src={valuations} />
                                 <p className="ms-3">Get More Leads</p>
                                </div>
                               
                             </div>
                           
                             <div style={{marginLeft:"50px"}}>
                             <div className="d-flex mb-4">
                               <img src={Feedback} />
                                 <p className="ms-3">Improve Engagement</p>
                               </div>
                                <div className="d-flex">
                                <img src={MRR}/>
                                 <p className="ms-3"> Increase Your ROI</p>
                                </div>
                             </div>
                         </div>

                         <button>GET STARTED IN A BLINK!</button>

                   </div>
                   <div className="col-md-6 text-center" data-aos="fade-right " data-aos-duration="700">


                       <img src={seoheader}/>



                   </div>
               </div>
           </div>



           </section>
    )
}

export default Header
