import React from 'react'
import seocheckimg from "../../images/SEO/seocheckimg.webp"

import fold3right from "../../images/SEO/fold3-right.webp"
const SeoCheckpoints = () => {
    return (
      <section className="seocheck">
     <div className="seo-check-bg">
     <img src={fold3right}/>
     </div>
     <div className="container">


       <div className="row seocheck-row"> 
       
       
       <div className="col-md-6" data-aos="fade-left" data-aos-duration="700">

<div className="seocheck-img">
<img src={seocheckimg}/>
</div>

</div>
<div className="col-md-6 sc-R" >

       <h2>Measurable SEO Checkpoints
For Maximum ROI
</h2>

<p>The higher you rank in results pages, the more clicks and traffic your site will generate.</p>
<p>Let’s all agree on one thing: We all trust search engines. Any query that pops in our heads or anything we are looking for online, the best place to look for it is a search engine. All you need to do is type your query and Voila! You get so many relevant results that sometimes it becomes overwhelming to decide which one to go with. An optimized online business website improves user experience, brings it top on search engine ranking, and tells the searchers that you are reliable. </p>


</div>
       </div>
     </div>



      </section>
    )
}

export default SeoCheckpoints
