import React , {  useEffect,useState } from "react"
import art3 from  "../../images/art3.webp"
import herobubble1 from "../../images/hero-buble1.webp"
import art1 from "../../images/art1.webp"
import art2 from "../../images/art2.webp"
import herogirl from "../../images/hero-girl.webp"
import { ZendeskAPI } from "react-zendesk"


// import { toast,ToastContainer } from "react-toastify"




const Header = () => {
  const initializeState={
    fullname:"",
    email:"",
    phone:"",
    socialmedia:""

  }


  const [state,setState]=useState(initializeState);
  const{fullname,email,phone,socialmedia}=state;
  useEffect(() => {
    ZendeskAPI("webWidget","hide");
  },[]);


  const handleInputchange=(e)=>{
    const {name,value}=e.target;
    setState({...state,[name]:value})
  }

  const openWidget=()=>{

    ZendeskAPI("webWidget","show");
    ZendeskAPI("webWidget","open");
    document.querySelector('#myLauncher').style.opacity = 0;
     ZendeskAPI("webWidget:on","close",function() {
      ZendeskAPI('webWidget', 'hide');
      document.querySelector('#myLauncher').style.opacity = 1;
    })
    
  

    
  }

  const storeData=(e)=>{
    
    e.preventDefault();
   
   
    if(!fullname  || !email || !phone || !socialmedia ){

    //  toast.error("Please fill out this field.")

    alert("All fields are required.")
      // toast.error("Fill All THE INPUR INFORMATION")
    }
else{
  // toast.success("Your Data saved")
  alert("Store Data.")

  setState({fullname:"",email:"",phone:"",socialmedia:"-- Select --"})

}


    
  }
    return (
    

<section className="Header">
{/* <ToastContainer position="top-center"/> */}
      <div className="particles_3 gt3_rotated_element">
            <img src={art3} alt="" width="41" height="53" />
          </div>
      <div className="container">
        <div className="row">
          <div className=" col-md-6 hero-v1">
          <img  className="hero-bubble" src={herobubble1} width="128" data-aos="fade-left" data-aos-duration="700"/>
            <div className="headerimg" data-aos="fade-up" data-aos-duration="900"> <img src={herogirl}  /> </div>
          </div>
          <div className="col-md-6  hero">
          <div className="particles_1 gt3_rotated_element">
            <img src={art1} alt="" width="41" height="53" />
          </div>
          <div className="particles_2 gt3_rotated_element">
            <img src={art2} alt="" width="41" height="53" />
          </div>
          
          
            <div className=" col-md-10">
            <h1> Digital 
            Marketing<br/>
            <span className="input"></span>
            {/* <!--Services--> */}
            
            </h1>
              <p> Your mere social media presence isn’t all it takes to
                up your social media game. Your Social Media
                footprint. Reach new heights with digital marketing
                services by <span className="marketingblink">Marketing Blink</span> </p>
            </div>
            <form onSubmit={storeData}  className="row HeaderInput">
			
			  <div className="col-md-6">
				<input 
        type="text"
         className="form-control" 
         placeholder="Full Name" 
       
         name="fullname"
         
         onChange={handleInputchange}
         
         value={fullname || ""}
      //  required
         
         />
			  </div>
			  <div className="col-md-6">
				<input
         type="email"
          className="form-control"
           placeholder="Email"
           
            name="email"
            
            onChange={handleInputchange}
         
            value={email || ""}
            // required
            />
			  </div>
			  <div className="col-md-6">
				<input 
        type="tel"
           
         className="form-control"
          placeholder="Phone" 
          
          name="phone"
          
          onChange={handleInputchange}
         
          value={phone || ""}
          // required
          
          
          />
			  </div>
			  <div className="col-md-6" >
				<select 
        className="form-control select"
         name="socialmedia"

         onChange={handleInputchange}
         
      
         name="socialmedia"
          
        //  required
         
         >
				  <option value={""}>-- Select --</option>	
				  <option value={"Social Media" || ""}>Social Media</option>
				  <option value={"SEO Services" || ""}>SEO Services</option>
				  <option value={"Paid Marketing" || ""}>Paid Marketing</option>
				  <option value={"Content Marketing" ||""}>Content Marketing</option>
				</select>
			  </div>
			  <div className="col-md-6">
				<input type="submit"  className="form-control  btnrequest " value="REQUEST A CALL BACK" />
			  </div>
			  <div className="col-md-6 vc">
				<input type="button" id="myLauncher"   onClick={openWidget} className="form-control  btnlive " value="LIVE CHAT" />
			  </div>
		 	
        </form>
          </div>
        </div>
      </div>
    </section>

   
     
    )
}

export default Header